@import url("https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@400;700&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Roboto Condensed", sans-serif;
}

.kepler {
  width: 100%;
  height: 100vh;
  position: fixed;
  background-color: black;
}

.container {
  width: calc(7 * 100vw);
  height: 100vh;
  background-color: black;
  position: absolute;
  transition: 4s all;
}

.imgs_div {
  display: inline;
  float: left;
  background-color: black;
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0px;
  padding: 0px;
  z-index: -10;
}

.imgs_div > img {
  display: inline;
  max-width: 100%;
  max-height: 100%;
  margin: 0px;
  padding: 0px;
}

.kepler_overDiv {
  width: 100vw;
  height: 100vh;
  position: absolute;
  display: flex;
  justify-content: center;
  z-index: 10;
}

.fa-caret-square-left {
  z-index: 10;
  position: absolute;
  left: 5%;
  top: 50%;
  transform: translate(-5%, -50%);
  font-size: 2rem;
  color: white;
  opacity: 0.3;
  transition: 2s all;
}

.fa-caret-square-right {
  right: 5%;
  z-index: 10;
  position: absolute;
  top: 50%;
  transform: translate(-5%, -50%);
  font-size: 2rem;
  color: white;
  opacity: 0.3;
  transition: 2s all;
}

.fa-caret-square-left:hover,
.fa-caret-square-right:hover {
  opacity: 1;
  cursor: pointer;
  transform: scale(1.4);
}

.kepler_txt {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  border: 1px solid white;
  color: white;
  padding: 20px;
  border-radius: 5px;
  background: rgba(241, 152, 152, 0.2);
}

/* Media Queris */

@media screen and (max-width: 550px) {
  /* Txt */

  .kepler_txt {
    font-size: 1rem;
  }
}

@media screen and (max-width: 400px) {
  /* Txt */

  .kepler_txt {
    font-size: .8rem;
    padding: 10px;
  }
}


@media screen and (max-width: 300px) {
  /* Txt */

  .kepler_txt {
    font-size: .6rem;
    padding: 3px;
  }
}


