@import url("https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@400;700&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Roboto Condensed", sans-serif;
}

.resPage_wrapper {
  width: 100%;
  height: auto;
}

.datePicker_div {
  width: 100%;
  height: 100%;
  background-color: black;
  display: grid;
  position: absolute;
  grid-template-columns: repeat(2, 1fr);
  place-items: center;
}

.datePicker {
  width: 27rem;
  height: 20rem;
  border: 1px solid white;
  border-radius: 5px;
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  position: relative;
}

.daysNum:hover {
  transform: scale(1.1);
  cursor: pointer;
  border: 1px solid blue;
}

.datePicker_txt {
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  text-align: center;
  width: 80%;
}

form {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
}

input {
  display: block;
  background-color: black;
  outline: none;
  padding: 0.8rem;
  float: left;
  border: none;
  color: white;
  font-size: 1.1rem;
}

button {
  font-size: 1rem;
  width: 5rem;
  background-color: black;
  color: white;
  padding: 0.8rem;
  border: none;
  position: relative;
  transition: 2s all;
  text-align: left;
  margin-top: 3rem;
}

button:hover {
  cursor: pointer;
  color: black;
  background-color: white;
}

.fa-caret-up {
  color: white;
  font-size: 2rem;
  animation-name: arrow_up;
  animation-duration: 2s;
  animation-timing-function: ease-in-out;
  animation-iteration-count: infinite;
  overflow: hidden;
  position: absolute;
  top: 90%;
}

/* Key Frames */

@keyframes arrow_up {
  0% {
    transform: scale(1);
  }

  100% {
    transform: scale(1.4);
  }
}

/* Media queris */

@media screen and (max-width: 800px) {
  .datePicker_div {
    grid-template-columns: none;
    grid-template-rows: repeat(2, 1fr);
    place-items: center;
  }

  .datePicker {
    margin-top: 1rem;
  }

  form {
    margin-top: 3rem;
  }

  input {
    text-align: center;
  }

  button {
    margin: 2rem auto;
  }

  .fa-caret-up {
    display: none;
  }

  .datePicker_txt {
    width: 60%;
    margin-bottom: 1rem;
  }
}

@media screen and (max-width: 450px) {
  .datePicker {
    width: 20rem;
    height: 15rem;
  }

  input {
    padding: 0.5rem;
    font-size: 0.9rem;
  }
}

@media screen and (max-width: 350px) {
  .datePicker {
    width: 15rem;
    height: 10rem;
  }

  input {
    padding: 0.2rem;
    font-size: 0.8rem;
  }

  .datePicker_txt {
    font-size: 0.8rem;
  }
}
