.cubes_div  {
  width: 100%;
  height: 100vh;
  display: grid;
  grid-template-columns: repeat(30, 1fr);
  position: absolute;
}

.cubes {
  background: white;
  display: inline-block;
  border-radius: 3px;
  box-shadow: 1px 1px 1pz grey;
}

.whiteSpan {
  background: transparent!important;
}