@import url("https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@400;700&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Roboto Condensed", sans-serif;
}

.aboutPage_wrapper {
  display: grid;
  place-items: center;
  width: 100%;
  height: 100vh;
  background-color: black;
}

.about_page_video {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  margin: 0;
  padding: 0;
  opacity: 0.4;
  object-fit: fill;
}

.about_page_txt {
  position: absolute;
  color: white;
  font-size: 2rem;
  z-index: 1000;
  width: 40rem;
  height: 20rem;
  text-align: center;
}

@media screen and (max-width: 650px) {
  /* About Page */

  .about_page_txt {
    font-size: 1.5rem;
    width: 30rem;
    height: 15rem;
  }
}

@media screen and (max-width: 500px) {
  /* About Page */

  .about_page_txt {
    font-size: 1.3rem;
    width: 25rem;
  }
}

@media screen and (max-width: 400px) {
  /* About Page */

  .about_page_txt {
    font-size: 1rem;
    width: 20rem;
  }
}

@media screen and (max-width: 350px) {
  /* About Page */

  .about_page_txt {
    font-size: 1rem;
    width: 15rem;
  }
}
