@import url("https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@400;700&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Roboto Condensed", sans-serif;
  overflow-x: hidden;
}

a {
  text-decoration: none;
  color: black;
}

ul li > a {
  color: white;
}

/* Main Div */

.fa-bars {
  position: absolute;
  top: 1%;
  left: 50%;
  transform: translate(-50%, -1%);
  cursor: pointer;
  font-size: 1.5rem;
  z-index: 100;
  color: black;
}

.fa-times {
  position: absolute;
  top: 1%;
  left: 50%;
  transform: translate(-50%, -1%);
  cursor: pointer;
  font-size: 1.5rem;
  z-index: 100;
  color: white;
}

.mainDiv {
  width: 100%;
  height: 100vh;
}

.mainDiv > h1 {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-weight: bold;
  font-size: 10rem;
  letter-spacing: 15px;
  background-image: url("../src/assets/Images/txtImg_6.png");
  background-clip: text;
  background-size: cover;
  color: transparent;
  animation-name: h1_anim;
  animation-duration: 4s;
  animation-timing-function: ease-in-out;
  animation-delay: 1s;
  animation-fill-mode: forwards;
  z-index: -100;
}

.fa-caret-down {
  position: absolute;
  left: 50%;
  top: 95%;
  transform: translate(-50%, -90%);
  font-size: 1.5rem;
  animation-name: arrow_down;
  animation-duration: 0.5s;
  animation-timing-function: ease-in-out;
  animation-iteration-count: infinite;
  animation-delay: 4s;
}

.nav_div > ul {
  text-align: center;
  display: grid;
  place-items: center;
  margin: 0rem auto;
}

.nav_div ul > li {
  list-style: none;
  font-size: 2rem;
  color: white;
  z-index: 100;
}

/* Second Div */

.second_div {
  width: 100%;
  height: 100vh;
  background: url("./assets/Images/txtImg_3.png");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  position: relative;
  opacity: 0;
  overflow-x: hidden;
}

.secondDiv_h2 {
  font-size: 2rem;
  margin: 5rem auto;
  position: absolute;
  left: 50%;
  top: 30%;
  transform: translate(-50%, -30%);
  text-align: center;
}

.learn_more:hover {
  background-color: black;
  color: white;
  cursor: pointer;
}

/* Third Div */

.third_div {
  width: 100%;
  height: 100vh;
  opacity: 0;
  overflow: hidden;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

.thirdDiv_left {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}

.thirdDiv_right {
  background: url("../src/assets/Images/kepler_mainImg.png");
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
}

.third_div h2 {
  font-size: 3rem;
}

/* Fourth Div */

.fourth_div {
  width: 100%;
  height: 100vh;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  opacity: 0;
}

.fourthDiv_left {
  background: url("../src/assets/Images/crew_1.jpg");
  background-size: cover;
  background-position: center;
}

.fourthDiv_right {
  display: grid;
  place-items: center;
  position: relative;
}

.fourthDiv_right > h2 {
  font-size: 3rem;
  margin-top: 8rem;
}

/* Fifth Div */

.fifth_div {
  width: 100%;
  height: 100vh;
  margin-top: 5rem;
  background: url("../src/assets/Images/fifth_div.png");
  background-position: center;
  background-size: cover;
  background-attachment: fixed;
  opacity: 0;
}

.fifth_div > div {
  width: 50%;
  height: 100%;
  position: relative;
  place-items: center;
}

.fifth_div h2 {
  font-size: 3rem;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

/* Footer */

.footer {
  width: 100%;
  height: 10rem;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  place-items: center;
  opacity: 0.7;
}

.sec_left {
  font-size: 2rem;
}

                                          /*  Key Frames  */

@keyframes h1_anim {
  0% {
    color: transparent;
  }

  100% {
    color: black;
  }
}

@keyframes arrow_down {
  0% {
    top: 95%;
  }

  100% {
    top: 96%;
  }
}

                                              /* Media Queris */

@media screen and (max-width: 700px) {
  /* Footer */

  .footer {
    padding-left: 1rem;
  }
}

@media screen and (max-width: 600px) {
  /* Main Div */

  .mainDiv > h1 {
    font-size: 5rem;
  }

  /* Second Div  */

  .secondDiv_h2 {
    font-size: 1.5rem;
    margin-bottom: 2rem;
  }

  /* Third Div */

  .third_div {
    grid-template-columns: none;
    grid-template-rows: repeat(2, 1fr);
  }

  .third_div h2 {
    margin-bottom: 4rem;
  }

  .thirdDiv_right {
    margin-bottom: 5rem;
  }

  /* Fourth Div */

  .fourth_div {
    grid-template-columns: none;
    grid-template-rows: repeat(2, 1fr);
    margin-top: 5rem;
  }

  .fifth_div > div {
    width: 60%;
  }

  .fourthDiv_right > h2 {
    margin-top: 2rem;
    margin-bottom: 6rem;
  }

  /* Footer */

  .footer {
    padding-left: 1rem;
  }

  .sec_left {
    font-size: 1rem;
  }
}

@media screen and (max-width: 350px) {
  /* Main Div */

  .mainDiv > h1 {
    font-size: 4rem;
  }

  /* Second Div  */

  .secondDiv_h2 {
    font-size: 1.2rem;
    margin-bottom: 2rem;
  }
}
