@import url("https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@400;700&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Roboto Condensed", sans-serif;
}

.crewPage_wrapper {
  width: 100%;
  height: 100vh;
  background-color: black;
}

.crewPage_video {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  margin: 0;
  padding: 0;
  object-fit: fill;
}

.crewCard_div {
  width: 90%;
  height: 100vh;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  place-items: center;
  left: 50%;
  transform: translate(-50%);
  position: absolute;
}

.crew_card {
  width: 15rem;
  height: 25rem;
  border: 1px solid white;
  border-radius: 5px;
  background: url("../../assets/Images/astronaut.jpg");
  background-size: cover;
  background-position: center;
  opacity: 0.5;
  position: relative;
  overflow: hidden;
}

.crew_card:hover {
  opacity: 1;
  cursor: pointer;
  transform: scale(1.1);
  transition: 2s all;
}

.crew_card > div {
  width: 100%;
  height: 35%;
  position: absolute;
  bottom: 0;
  text-align: center;
  color: white;
}

.crew_card p {
  padding-top: 1rem;
}

.crew_card h3 {
  margin-top: 0.5rem;
}

/* Media queris */

@media screen and (max-width: 1070px) {
  /* Crew cards */
  .crewCard_div {
    /* grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr); */
  }

  .crew_card {
    width: 12rem;
    height: 17rem;
  }

  .crew_card > div {
    width: 100%;
    height: 100%;
    top: 50%;
  }

  .crew_card p {
    padding-top: 0rem;
  }

  .crew_card h3 {
    margin-top: 0rem;
  }
}

@media screen and (max-width: 870px) {
  /* Crew cards */
  .crewCard_div {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
  }

  .crew_card {
    width: 10rem;
    height: 15rem;
  }
}

@media screen and (max-width: 400px) {
  /* Crew cards */
  .crewCard_div {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
  }

  .crew_card > div {
    width: 100%;
    height: 100%;
    top: 40%;
  }

  .crew_card {
    width: 8rem;
    height: 12rem;
  }
}

@media screen and (max-width: 400px) {
  /* Crew cards */
  .crew_card p {
    padding-top: 0rem;
    font-size: 0.8rem;
  }

  .crew_card h3 {
    margin-top: 0rem;
  }
}

@media screen and (max-width: 300px) {
  /* Crew cards */
  .crew_card {
    width: 6rem;
    height: 10rem;
  }
  
  .crew_card p {
    padding-top: 0rem;
    font-size: 0.6rem;
  }

  .crew_card  h3 {
    margin-top: 0rem;
    font-size: .8rem;
  }
}
